.addToCart {
  display: flex;
  justify-content: space-between;
  align-items: center;

  width: 100%;
  height: 40px;

  &--add {
    padding: 9px 45px;

    background-color: #313237;

    font-size: 14px;
    font-weight: 700;
    line-height: 21px;
    color: white;
    border: none;

    cursor: pointer;

    &:hover {
      box-shadow: 0px 3px 13px 0px #17203166;
    }
  }

  &--active-add {
    padding: 9px 45px;

    background-color: #fff;

    font-size: 12px;
    font-weight: 600;
    line-height: 15px;
    color: #27AE60;

    border: 1px solid #E2E6E9;

    cursor: pointer;
  }

  &--favorite {
    padding: 9px 11px;

    border: 1px solid #B4BDC3;
    cursor: pointer;

    &:hover {
      border: 1px solid #313237
    }
  }

  &--active-favorite {
    padding: 9px 11px;

    border: 1px solid #E2E6E9;

    cursor: pointer;
  }

  &--icons {
    height: 16px;
    width: 16px;
  }
}