@import "../../assets/normalize";
@import '../../assets/colors.scss';
@import '../../assets/variables';
@import "../../assets/mixins";

.mainPageContainer {
  padding: 0 16px;

  @include onLargeDesktop {
    padding-left: 152px;
    padding-right: 152px;
  }
}

.mainTitle {
  @include headline-1;
  color:$primary;
  padding-top: 24px;

  margin-bottom: 24px;

  @include onTablet {
    padding-top: 56px;
    margin-bottom: 56px;
  }
}

.section{
  margin-bottom: 32px;
}

.sectionTitle {
  @include headline-2;
  color: $primary;

  margin-bottom: 24px;
}

.categoriesContainer {
  @include pageGrid;
}

.categoryImg {
  width: 100%;
  max-height: 100%;
  margin-bottom: 24px;
  border-radius: 7px;
  transition: transform 0.3s ease;
}

.categoryImg:hover {
  transform: scale(1.03);
}

.categoryTitle {
  color: $primary;
  @include headline-3;
  margin-bottom: 4px;
}

.categorySubtitle{
  @include secondaryText;
  font-weight: 600;
  color: $secondary;
  margin-bottom: 32px;
}



